.character-card {
  width: 10%;
  flex-grow: 1;
  text-align: center;
}

.character-card__image-container {
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05), 0 1px 3px 1px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.character-card__image {
  height: auto;
  width: auto;
  object-fit: cover;
  max-height: 12rem;
}


.guess-who-logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
}

.about-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.menu {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    padding-bottom: 20px;
}

.menu button {
    font-weight: bold;
    color: #fff;
    margin: 5px;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    background-color: red;
}

.menu button:hover {
    background-color: blue;
}

.instructions {
    text-align: center;
    justify-content: center;
}

.chat {
    margin-top: 20px;
}

.dropdown-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.dropdown-item {
    margin: 0 5px;
}

.dropdown-item select,
.dropdown-item button {
    height: 40px;
}

.result {
    text-align: center;
    font-weight: bold;
    margin-top: 10px;
}

.chat-container {
    width: 600px;
    height: 250px;
    background-color: #f1f1f1;
    overflow: hidden;
    border-radius: 5px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto;
}

.chat-messages {
    flex-grow: 1;
    overflow-y: auto;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 80%;
}

.question {
    background-color: #e8f4ff;
    color: #333;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    align-self: flex-end;
    text-align: right;
    word-wrap: break-word;
    margin-right: auto;
    margin-left: 300px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.answer {
    background-color: #fff;
    color: #333;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    align-self: flex-start;
    text-align: left;
    word-wrap: break-word;
    margin-right: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.chat-input {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.dropdown-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.dropdown-container select,
.dropdown-container button {
    height: 40px;
    margin-right: 10px;
}

.dropdown-container input {
    width: 400px;
    height: 25px;
    padding: 5px;
    font-size: 14px;
}

body {
    background-color: lightblue;
}
  

.gameboard_container {
    padding-top: 20px;
    padding-left: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    width: 610px;
    margin: 0 auto;
    float: left;
    border: 10px solid red;
    border-radius: 16px;
    background-color: black;
}
